import React,{Component} from 'react';
import './index.less';
export default class OrderListItem extends Component{
    constructor(props){
        super(props);
        this.state={
            commentState:props.data.commentState
        }
    }
    changeCommentState(){
        this.setState({
            commentState:1
        })
    }
    cancel(){
        this.setState({
            commentState:0
        })
    };
    sure(){
        this.props.commitComment(this.props.data.id,this.comment.value,()=>{
            this.setState({
                commentState:2
            })
        });
    };
    render(){
        let{id,img,title,count,price}=this.props.data;
        return(
            <div>
                <div className="order-list-item">
                    <img src={img} alt=""/>
                    <div className="order-list-content">
                        <h3>{title}</h3>

                        <p>{count}</p>
                        <p>{price}</p>

                    </div>
                    <div className="comment-btn">
                        {/*commentState  0 1 2*/}
                        {
                            this.state.commentState===0?
                                <button className="back" onClick={this.changeCommentState.bind(this)}></button>:
                                this.state.commentState===1?
                                    '':<button></button>

                        }
                    </div>
                </div>
                {
                    //
                    this.state.commentState===1?
                        <div className="comment-area">
                            <textarea cols="30" rows="10" ref={ref=>this.comment=ref}></textarea>
                            <div>
                                <button onClick={this.sure.bind(this)}></button>
                                <button onClick={this.cancel.bind(this)}></button>
                            </div>
                        </div>:''
                }

            </div>
        )
    }
}


