import React,{Component} from 'react';
import './index.less';
export default class ChangeCity extends Component{
    render(){
        return(
            <div className="change-city">
                <h3></h3>
                <ul className="change-city-list" onClick={(event)=>{this.props.changeCity(event.target.innerText)}}>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
            </div>
        )
    }
}


